.products {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: space-between;
  margin-top: 10px;
  padding: 5px;
}
.product__price{
  font-weight: bold;
}
.product__image{
  cursor: pointer;
}
.product{
  margin-top: 5px;
  border-bottom: 5px solid rgb(253, 218, 177);
}
