.header {
  display: flex;
  width: 1200px;
  height: 40px;
  justify-content: space-between;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 20px;
  box-sizing: border-box;
  background-color: rgb(253, 218, 177);

}

.headerName {
  font-weight: 900;
  font-size: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.shops {
  display: flex;
}
.tel {
  margin-right: 100px;
}
.telmail {
  display: flex;
  flex-direction: column;
}
.telmail a {
 font-size: 14px;
 color: black;
 text-decoration: none;
}

.sweet{
    font-size: 20px;
    font-weight:normal ;
    margin-left: 30px ;
}

.cart_icon{
  height: 35px;
}

.cart_container{
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}